<template>
 <div>
     <el-dialog
            title=""
            width="750px"
            :visible.sync="visible"
            :append-to-body="true"
            :close-on-click-modal="false"
            @close="$emit('update:show', false)"
            :show="show">
            <div class="pd-10">
                <el-table
                        ref="multipleTable"
                        :data="tableData3"
                        tooltip-effect="dark"
                        style="width: 100%"
                        :max-height="600"
                        border
                        >
                        <el-table-column
                            fixed="left"
                            label=""
                            width="100">
                            <template slot-scope="scope">
                                <el-button @click="handleClick(scope.row)" size="mini"></el-button>
                            </template>
                          </el-table-column>
                        <el-table-column prop="date" label="" width="120" show-overflow-tooltip></el-table-column>
                        <el-table-column prop="address" label="" show-overflow-tooltip></el-table-column>
                        <el-table-column prop="name" label="" width="120"></el-table-column>
                    </el-table> 
                </div>
      </el-dialog>
 </div>
</template>

<script>
 import { getMenu, getSession } from "@/utils/util.js";
 export default {
   data () {
     return {
          visible:this.show,
                  tableData3: [{
          date: '2016-05-03',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-02',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-04',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-01',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-08',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-06',
          name: '',
          address: ' 1518 '
        }, {
          date: '2016-05-07',
          name: '',
          address: ' 1518 '
        }],
     }
   },
   methods:{
      handleClose(){
         this.visible = false; 
      },
      handleClick(row) {
        this.$emit('handlemodifyZbr',row);
      }
   },
   props:{
       show:{
           type:Boolean,
           default:false
       },
       xmbh:{
          type:String,
          default:''      
       }
   },
     watch: {
        show (n,o) {
            this.visible = this.show;
            if(!n){
              
            }else{
                this.$get(this.API.listZbxx,{xmbh:this.xmbh}).then(res=>{
                    if(res.state == 'success'){
                        console.log(res)
                    }
                })
            }
        }
    },
   components: {}
 }
</script>

<style scoped>

 
</style>



